/* Common Css Reuseable */

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background: none repeat scroll 0 0 #fff;
  color: #1d3557;
  overflow-x: hidden;
  font-family: SofiaPro,Arial,sans-serif;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.5;
  letter-spacing: normal;
  font-display: swap;
}
.flex-list{
  display: flex;
  justify-content: space-between;
 flex-flow: nowrap;
 align-content: space-evenly;

}
.container{
  width: 100%;
  margin-right: auto;
  margin-left: auto; 
}
ul{
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.full-image img{
  width: 100%;
}
/* Common Css Reuseable End*/

/* Top Banner Css Start */
.top_banner{
  width: 100%;
  background-color: white;
  padding: 28px 10px;
}
.top_banner_logo{
  float: left;
  margin-top: -10px;
}
.top_banner_list{
  float:right;
  margin-top: -10px; 
}
.top_banner_logo>li, 
.top_banner_list>li{
  padding: 0px 20px;
  
}
/* Top Banner Css End */

/* Top Ribbon Css Start */
.sticky-navbar{
  position: sticky;
  TOP: 0px;
  background: white;
  box-shadow: 8px 10px 12px #22222252;
}
.banner_logo{
  width: 100px;
}
.banner_logo img{
  width: 100%;
}

.top_banner_ribbon{
  background-color: #f2b721;
  padding: 8px 16px;
  color: #000;
  text-align: center;
  font-weight: 400;
  min-height: 30px;
  width: 100%;
}
/* Top Ribbon Css Start */

/* Main Logo List Start */ 

.main_logo_list{
  padding: 20px 0px;
}
.search_form {
  position: relative;
  clear: both;
  margin: 0 auto;
  left: 50px;
}
.header-search input[type=text] {
  width: 100%;
  padding: 1px 16px 1px 20px;
  height: 32px;
  width: 140%;
  outline: none;
  border: none;
  color: #13446f;
}
.header-search input[type=text]::placeholder {
  color: #0056a2;
  opacity: 1;
}
.header-search button {
  position: absolute;
  left: -25px;
  background-color: transparent;
  border: none;
  padding: 0;
  line-height: 30px;
  line-height: 1.875rem;
  min-height: 0;
  top: 6px;
}
.login_bar{
  flex-grow: 0.2;
}
.bi-search::before {
  content: "\f52a";
  font-size: 1.2rem;
  color: #0056a2;
}
.web_logo img{
  width: 150px;
}
.search_bar,.login_bar{
  margin-top: 25px;
}
.icon-party img{
  width: 24px;
  height: 17px;
}
/* Main Logo List Start */ 

/* Middle Ribbon List Start */ 
.Shipping_rates{
  background-color: #6E951E;
  padding: 4px 16px;
  color: #ffffff;
  text-align: center;
  font-weight: 400;
  min-height: 30px;
  width: 100%;
}
/* Middle Ribbon List End */ 
/* Navbar List Start */
.navbar_list{
  font-weight: 600;
  color: #1d3557;
  justify-content: space-evenly;
  padding: 0px 120px;
  cursor: pointer;
}
.navbar_list>li:hover{
  color: #0056a2;
  text-decoration: underline;
}
      /* Dropdown Menu */

.dropdown-wrapper{
  position: relative;
  
}
.main-submenu-wrapper{
  width: 1100px;
  box-sizing:border-box;
    transition: 0.3s ease-in-out;
    transition-delay: 0.1s;
  
}
.dropdown-wrapper .main-submenu-wrapper{
  /* display: none; */
  opacity: 0;
  position: absolute;
  top: 100%;
  background-color: #fff;
  padding: 75px 10px;
  transition: opacity 0.3s ease;
  z-index: 100;
}
.main-submenu-wrapper{
  left: -240px;
  
}
.stuffed-animal-clothing{
  left: -435px ;
}
.characters{
  left: -650px ;
}
.sale{
  left: -755px 
}
.new_arrivals{
  left: -820px ;
}
.kids_adults{
  left: -938px ;
}
.dropdown:hover .main-submenu-wrapper{
  /* display: block; */
  opacity: 1;
  transition: all 0.7s ease;
  
}
.see-all-link{
  display: block;
  top: 0px;
  left: 18px;
  padding: 6px 0px;
  margin-top: 14px;
  font-size: 1rem;
}
.vertical-menu li{
  padding: 2px 10px;
  color: #1d3557;
  text-decoration: none;
  font-size: 0.75rem;
  font-weight: 500;
}
.vertical-menu > li:first-child {
  color: gray;
  pointer-events: none;
  cursor: default;
}
.vertical-menu > li:nth-child(2) {
  font-weight: bolder !important;
  font-size: 0.89rem !important;
}

.vertical-menu li:hover,.see-all-link:hover {
  color: #840621;
}
/* Submenu columns */
.navbar-submenu-col1,
.navbar-submenu-col2,
.navbar-submenu-col3,
.navbar-submenu-col4,
.navbar-submenu-col5,
.navbar-submenu-col6 {
display: inline-block;
vertical-align: top;
width: calc(100% / 7);
}
/* Navbar List End */


/* Body Section start */
.birthday-logo,.birthday-banner{
  text-align: center;
}
.birthday-logo{
  width: 60%;
  margin: 0 auto;
}
.birthday-logo img{
  width: 100%;
}

.birthday-banner-text{
  color: #00b2e3;
  font-size: 2.5rem;
  padding: 0 0 0.5rem 0;
  font-weight: normal;
}
.text-info{
  color: #000;
  font-size: 1.5rem;
  padding: 0.25rem 0;
  text-align: center;
}
.special-note{
  color: #000;
  font-size: 0.93rem;
  padding: 0.25rem 0;
}
.birthday-banner img,.birthday-banner{
      width: 100%;
      text-align: center;
      margin: 0 auto;
}
.lightblue{
  color: #00b2e3 ;
}
.buttons{
  text-align: center;
  margin-top: 10px;
}
.orange {
  background-color: #f99e23;
  font-size: 1.75rem;
  font-weight: bold;
  padding: 0.75em 0;
  border: none;
  border-radius: 5px;
  width: 245px;
  margin: 5px;
  text-align: center;
}
.orange a{
  text-decoration: none;
  color: #fff;
}
.special-link{
  color: #0056a2;
}

.beargiftshop {
 text-align: center;
 margin-top: 35px;
}
.gift-flex-list{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  
}
.gift-flex-list>li{
  width: calc(100%/4.3);
  border: 1px solid #0056a2; 
}
.gift-flex-list>li>img{
  width: 100%;
}
.list-info{
  padding: 20px 15px;
  text-align: center;
}
.list-info h2,h5, a{
  padding: 14px 0;
}
.gift-button{
  background-color: white;
  border: 1px solid #0056a2;
  color: #0056a2;
  padding: 15px;
  border-radius: 5px;
}
/* Forth Section Start */
.multicolor-font{
  text-align: center;
  margin-top: 15px;
  font-size: 1.5rem;
  text-align: center;
  padding: 3px;
  word-spacing: 7px;
}
      
/* Forth Section Start END */

/* Fifth Section START */
.countcandles-container{
  text-align: center;
}
.green-hr{
  padding: 3rem;
}
.candle-logo{
  width: 75%;
}
.faqs{
  text-align: left; 
  padding: 0 55px; 
  margin: auto;
}
.faqs>h3{
  font-size: 2rem;
  padding-bottom: 15px;
}
.faqs>p{
  color: #000;
  font-size: 1.5rem;
  padding: 1rem 0;
  line-height: 2.5rem;
  word-spacing: 3px;
  word-break: break-word;

}
.faqs>strong{
  font-weight: 700;
}
p.terms {
  font-size: 1em;
  padding: 50px;
}
/* Fifth Section END */

      

/* Body Section End */

/* Footer section start */
.footer-ribbon {
  background: #F8F8F8;
  padding: 32px 16px;
  text-align: center;
}
.footer-row {
  background-color: #0b5a9f;
  color: white;
  padding: 9px 0px;
  height: 110px;
  text-align: left;
  width: 75%;
}
.footer-row>div{
  float: left;
  padding: 0px 30px
}
.footer-button{
  color: #0056a2;
  text-shadow: none;
  background-color: #fff;
  border-color: #0056a2;
  max-width: 200px;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
}
.vertical-align{
  vertical-align: middle;
  margin-top: 5px;
}
.vertical-align>h3,p{
  padding: 4px 0;
}
.button-block{
  margin-top: 30px;
}
.content-asset p{
  padding: 0 0 16px;
  margin: 0;
  line-height: 1em;
  font-size: 1rem;
}

/* Footer section End */
/* First Section */
.footer-heart{
  width: 30px;
  vertical-align: middle;
}
.footer-list{
  padding: 10px 70px;
  justify-content: flex-start;
}
.footer-list p{
  padding: 0 0 16px;
  margin: 0;
  line-height: 1em;
}
.footer-list>li:first-child {
  width: 42%;
}
.footer-list>li:not(:first-child) {
  width: 18%;
  line-height: 1.8;
}
.footer-list>li>h2{
  font-size: 1.25rem;
  margin-bottom: 15px;
}
.subscribe-form input[type=email]{
  box-sizing: border-box;
  padding: 0 10px;
  border: 1px solid #b7b7b7;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-size: .875rem;
  text-indent: 0;
  color: #1d3557;
  background-color: #fff;
}
.button{
  cursor: pointer;
  display: inline-block;
  box-sizing: border-box;
  height: auto;
  padding: 8px;
  gap: 8px;
  font-family: SofiaPro,Arial,sans-serif;
  font-weight: 400;
  font-size: 12px;
  font-size: .75rem;
  letter-spacing: normal;
  border-radius: 4px;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  transition: all .3s ease;
  min-width: 80px;
  color: #fff;
  background-color: #e62144;
  border: 1px solid #e62144;
  max-width: 286px;
}
.form-section>div{
  float: left;
}
.subscribe-form{
  width: 60%;
}
.button-wrap{
  padding-left: 30px;
}
.checkbox-wrapper{
  margin-top: 50px;
}
.checkbox+label:before {
  position: absolute;
  top: calc((1em - 22px)/5);
  left: 0;
  /* background: url(./images/checkbox.svg) no-repeat 50%; */
  border-radius: 4px;
  width: 22px;
  height: 22px;
  
}
.flag-icon-ca{
  /* background-image: url(./images/canada-flag.svg); */
}
.share-buttons{
  padding: 30px 0;
}
.social-bugs li{
  display: inline-block;
  padding: 0 12px;
}
.copyright{
  background-color: #0056a2;
  width: 100%;
  height: 85px;
  color: white;
  border-top: 1px solid #b7b7b7;
}
.copyright a{
  color: white;
  text-decoration: none;
}
.privacypolicy-list{
  justify-content: space-evenly;
  padding: 6px 3px;
}
.privacypolicy-list li:hover a{
  text-decoration: underline;
}
.reserved-rights{
  text-align: center;
  vertical-align: middle;
  padding: 14px 0;
}
.footer-bottom-wrapper{
  border-top:1px solid #b7b7b7;
}

/* Responsive Media Query */
@media (min-width: 1340px){ 
  .container {
      max-width: 1240px;
  }
  .navbar_list>li{
      padding: 0px 3px 13px;
  }
  .footer-row{
      width: 55%;
  }

}
@media (min-width: 1200px)and (max-width: 1339px){ 
  .container {
      max-width: 1140px;
  }
  .navbar_list>li{
      padding: 0px 3px 13px;
  }
}
@media (min-width: 992px)and (max-width: 1199px){ 
  .container {
      max-width: 960px;
  }
  .navbar_list{
      padding: 0px 40px;
  }
  
}
@media (min-width: 768px)and (max-width:991px){ 
  .container {
      max-width: 720px;
  }
  .ribbon_info{
      font-size: 0.8rem;
  }
  .top_banner_list>li>p{
      font-size: 0.8rem;
  }
  .navbar_list{
      padding: 0px 8px;
      font-size: 12px;
  }
  .birthday-banner-text{
      font-size: 2rem;
  }
  .text-info{
      font-size: 1.25rem;
  }
  .special-note{
      font-size: 0.85rem;
  }
  .birthday-treat{
      width: 75%;
      margin: 0 auto;
  }
  .orange{
      font-size: 1.25rem;
      width: 200px;
  }
  .giftshop{
      width: 75%;
      margin: 0 auto;
  }
  .list-info h2{
      font-size: 1rem;
  }
  .list-info h5{
      font-size: 0.678rem;
  }
  .list-info h2, h5, a {
      padding: 8px 0;
  }
  .gift-button{
      padding: 6px;
      font-size: .75rem;
  }
  .multicolor-font img{
      vertical-align: middle;
      padding: 0px 10px;
      width: 40px;

  }
}
@media (min-width: 576px)and (max-width:767px){ 
  .container {
      max-width: 540px;
  }
  .ribbon_info{
      font-size: 0.8rem;
  }
  .top_banner_list>li>p{
      font-size: 0.8rem;
  }
  .top_banner_logo>li, .top_banner_list>li {
    
      padding: 0px 7px;
  }
  .banner_logo{
      width: 65px;
  }
}
@media (max-width: 575px){ 
  .container {
      max-width: 540px;
  }
  .ribbon_info{
      font-size: 0.6rem;
  }
  .top_banner_list>li>p{
      font-size: 0.6rem;
  }
  .top_banner_logo>li, .top_banner_list>li {
    
      padding: 0px 10px;
  }
  .banner_logo{
      width: 55px;
  }
}


